import React, { useEffect, useRef, useCallback, useState} from 'react';
import {connect} from 'react-redux';
import SliceWaveform from 'components/waveform/SliceWaveform.js';
import {getStemsData} from 'stems/getStems.js';
import './recorder.scss';

const SliceRecorder = ({playerRef, zorbId, setIsSlicing}) => { 
    let [screenshot, setScreenshot] = useState();
    let [buffer, setBuffer] = useState();
    let [waveform, setWaveform] = useState();
    let [silence, setSilence] = useState();
    let [startStep, setStartStep] = useState();
    let loopsDataRef = useRef({});
    let [loopsData, setLoopsData] = useState();
    let [stemsData, setStemsData] = useState();

    useEffect(() => {
        console.log("pre loops data=", loopsData);
        if (!loopsData || !loopsData.drums || !loopsData.synths) {
            return;
        }

        console.log("loops data=", loopsData);
        getStemsData(loopsData).then(setStemsData);

    }, [loopsData]);

    useEffect(() => {
        if (buffer) {
            setIsSlicing(true);
        }
    }, [buffer, setIsSlicing]);

    const onClick = useCallback(() => {
        playerRef.current.contentWindow.postMessage({
            type: "slice-audio",
            body: ""
        }, "*");
    }, [playerRef]);

    useEffect(() => {
        window.addEventListener("message", onMessage);
        return () => window.removeEventListener("message", onMessage);
    }, [loopsData, setWaveform, setBuffer, setScreenshot, setLoopsData]);
    
    const onMessage = useCallback((e) => {
        if (e.data.type === "drum-loops-data") {
            console.log("drums found=", e.data.body);
            let data = {
                ... loopsDataRef.current,
                drums: e.data.body
            };
            loopsDataRef.current = data;
            setLoopsData(data);
        } else if (e.data.type === "synth-loops-data") {
            console.log("synth found=", e.data.body);
            let data = {
                ... loopsDataRef.current,
                synths: e.data.body
            };
            loopsDataRef.current = data;
            setLoopsData(data);
        }
        if (e.data.type === "zpore-screenshot") {
            setScreenshot(e.data.body);
        }
        if (e.data.type === "slice-audio-buffer") {
            setBuffer(e.data.body);
        }
        if (e.data.type === "slice-silence") {
            setSilence(e.data.body);
        }
        if (e.data.type === "slice-start-step") {
            setStartStep(e.data.body);
        }
        if (e.data.type === "slice-audio-waveform") {
            setWaveform(e.data.body);
        }
    }, [loopsData, setWaveform, setScreenshot, setBuffer, setStartStep, setLoopsData]);

    return [
        <div
          onClick={onClick}
          className="pushable slice-recorder">
        </div>,
        waveform ? <SliceWaveform
                     zorbId={zorbId}
                     screenshot={screenshot}
                     silence={silence}
                     startStep={startStep}
                     stemsData={stemsData}
                     onCancel={() => {
                         setBuffer(undefined);
                         setWaveform(undefined);
                         setIsSlicing(false);
                     }}
                     buffer={buffer} waveform={waveform}/> : ''
    ];
};

const mapStateToProps = (state, props) => {
  return {
      playerRef: state.spore.playerRef
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(SliceRecorder);
