import ENSName from 'components/ens/ENSName.js';
import React, { useEffect, useCallback, useState} from 'react';
import {connect} from 'react-redux';
import LoadingRemix from './LoadingRemix.js';
import Zorb from './Zorb.js';
import './currently-playing.scss';

const CurrentlyPlaying = ({loading, playing}) => { 
    if (!playing) {
        return '';
    }
    let zorbId = playing && playing.tokenURI.zorbId;
    console.log("PLAYING=", playing);
    return (
        <div className="currently-playing">
          {loading && <LoadingRemix/>}
          {playing && !loading && <Zorb className="zorb" zorbId={zorbId}/>}
          <ENSName address={playing.to}/>

        </div>);
};

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(CurrentlyPlaying);
