import BackendQuery from 'decentralized/BackendQuery.js';
import {ethers} from 'ethers';
import {ERC721Transfer} from 'decentralized/EthereumEvents.js';
import {web3} from 'decentralized/web3.js';
import {DROP_CONTRACT} from './zpore.js';
import {creator} from './abi/zpore-drop-creator.js';
import {ERC721} from './abi/erc721.js';


export const getZporeRemixes = async () => {
    let query = new BackendQuery(DROP_CONTRACT)
        .from(ERC721Transfer)
        .select('tokenIds');

    let results = await query.executeQuery();

    // now for each of these need to call the tokenURI function from the DROP_CONTRACT
    for (let  result of results ){
        let tokenURI = await getTokenURI(result.tokens, DROP_CONTRACT);
        result.tokenURI = tokenURI;
    }
    return results;
};


export const getTokenURI = async (tokenId, address) => {
    const contract = new web3.eth.Contract(ERC721.abi, address);

    const result = await contract.methods.tokenURI(tokenId).call();
    return decode(result);
}

const decode = (uri) => {
    let starter = 'base64,';
    if (uri.indexOf(starter) >= 0) {
        let str = atob(uri.slice(uri.indexOf(starter) + starter.length));
        return JSON.parse(str);
    }
    return "";
};
