import {triggerRefresh} from 'backend/cacheServer.js';
import Web3 from 'web3';
import {ethers} from 'ethers';
import {creator} from './abi/zpore-drop-creator.js';


const PINATA_BASE = "https://zequencer.mypinata.cloud/ipfs/";

// aka where the tokens will go--> generated by the zora drop creator contract, which was passed the
// custom metadata renderer and minter
export const DROP_CONTRACT = "0xdAA94fBd00C9a23B4e3b4f684d1103Ff1dDfF972".toLowerCase();
const {deployed, abi} = creator;

// the deployed creator contract contains the "purchase" function that we are gonna call, and pass
// the drop contract (i.e. where the tokens go)

export const newZpore = async (signer, remixURI, coverURI, caption, zorbId=1, stemsData) => {
    console.log("STEMS DATA=", stemsData);
    remixURI = PINATA_BASE + remixURI;
    coverURI = PINATA_BASE + coverURI;
    const contract = new ethers.Contract(deployed.goerli, abi, signer);
    let args = [
            remixURI,
            coverURI,
            caption,
            zorbId,
            stemsData.stemIds, // stemIds
            stemsData.percentages, // percentages
    ];
    console.log("ARGS TO TRANSACT=", args);
    const tx = await contract.purchase(
        DROP_CONTRACT,
        args,
        {
            value: 0
        });

    console.log('purchase returned', tx);
    console.log('waiting...');
    let transactionHash = tx.hash;
    triggerRefresh(transactionHash, DROP_CONTRACT);
    return tx;
};
